<template>
  <div class="popular">
    <!-- <transition> -->
    <PopulatsItem
      :item="item"
      :slideview="slideview"
      :index="index"
      :next="populars[index + 1 == populars.length ? 0 : index + 1]"
      v-for="(item, index) in populars"
      :key="index"
      @change-slide="changeSlide"
    />
    <!-- </transition> -->
  </div>
</template>

<script>
import PopulatsItem from "./PopulatsItem.vue";
export default {
  components: {
    PopulatsItem,
  },
  data() {
    return {
      slideview: 0,
      timeout: null
    };
  },
  async mounted() {
    await this.$store.dispatch("popular/getPopulars");
    this.timeout = setTimeout(this.slide, 10000);

  },
  computed: {
    populars() {
      return this.$store.getters["popular/populars"].results;
    },
  },
  methods: {
    slide() {
      if (this.populars.length == this.slideview) {
        this.slideview = 0;
      } else {
        this.slideview++;
      }
      this.timeout = setTimeout(this.slide, 10000);
    },
    changeSlide() {
      clearTimeout(this.timeout)
      if (this.populars.length == this.slideview) {
        this.slideview = 0;
      } else {
        this.slideview++;
      }
      this.timeout = setTimeout(this.slide, 10000);
    }
  },
};
</script>