import { createApp } from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import ru from '@/locales/ru'
import en from '@/locales/en'
import './style/main.scss'
import { createI18n } from 'vue-i18n'
// import { /* loadLocaleMessages, */ setI18nLanguage, SUPPORT_LOCALES } from '@/i18n'

const messages = {ru, en}

const i18n = createI18n({
    locale: 'ru',
    fallbackLocale: 'en',
    messages
})
router.beforeEach(async (to, from, next) => {
    localStorage.lang = localStorage.lang ?? 'ru'
    i18n.global.locale = localStorage.lang    
    return next()
  })

createApp(App).use(i18n).use(router).use(store).mount('#app')
