<template>
  <div class="movies">
    <router-link to="/movies" class="movies__title">
      {{ $t("films.title") }}
      <span class="material-icons"> arrow_forward_ios </span>
    </router-link>
    <div class="scroll-items">
      <div
        class="scroll-items__prev"
        @click="prev"
        :class="{ view: scrollPosition > 0 }"
      >
        <span class="material-icons"> arrow_back_ios </span>
      </div>
      <div class="movies__list" ref="list">
        <MoviesView
          @click="getMovie(movies[index], index)"
          v-for="(movie, index) in movies"
          :movie="movie"
          :key="index"
          :selectId="selectId"
        />
        <router-link to="/movies" class="movies__list-item">
          <span class="material-icons"> arrow_forward_ios </span>
          {{ $t("films.all") }}
        </router-link>
      </div>
      <div
        class="scroll-items__next"
        @click="next"
        :class="{ view: scrollPosition < wscrollPosition }"
      >
        <span class="material-icons"> arrow_forward_ios </span>
      </div>
    </div>
    <div class="movies-full" :class="{ active: selectId != null }">
      <button class="movies-full__close" @click="close">
        <span class="material-icons">close</span>
      </button>
      <div class="movies-full__content" v-if="selectMovie != null">
        <img
          :src="`https://www.themoviedb.org/t/p/w1920_and_h800_multi_faces/${selectMovie.backdrop_path}`"
          class="movies-full__content-background"
          alt=""
        />
        <div class="movies-full__content-data">
          <div class="movies-full__content-datamovie">
            <h2 class="movies-full__content-title">
              {{ this.selectMovie.title }}
            </h2>
            <p class="movies-full__content-overview">
              {{ this.selectMovie.overview }}
            </p>
            <p class="movies-full__content-info">
              <span
                >{{ this.selectMovie.release_date }}
                <span v-for="(item, index) in selectMovie.genres" :key="index"
                  >{{ item.name
                  }}<span v-if="index < selectMovie.genres.length - 1"
                    >,
                  </span></span
                ></span
              >
              <span>{{ selectMovie.runtime }}</span>
            </p>
            <div class="movies-full__content-casts">
              <CastItems :count="4" :id="selectMovie.id" :type="'castfilms'" />
            </div>
            <BtnMore :id="selectMovie.id" :page="'movies'" />
          </div>
        </div>
      </div>
      <div class="movies-full__scelet">
        <div class="movies-full__scelet-background"></div>
        <div class="movies-full__scelet-data">
          <div class="movies-full__scelet-datamovie">
            <h2 class="movies-full__scelet-title"></h2>
            <div class="movies-full__scelet-overview">
              <div class="movies-full__scelet-overview1"></div>
              <div class="movies-full__scelet-overview2"></div>
              <div class="movies-full__scelet-overview3"></div>
            </div>
            <p class="movies-full__scelet-info">
              <span></span>
              <span></span>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import MoviesView from "./MoviesView.vue";
import CastItems from "../casts/CatsItems.vue";
import BtnMore from "../BtnMore.vue";
export default {
  components: {
    MoviesView,
    CastItems,
    BtnMore,
  },
  data() {
    return {
      scrollPosition: 0,
      wscrollPosition: 0,
      selectId: null,
      selectMovie: null,
      index: null,
    };
  },
  async mounted() {
    await this.$store.dispatch("movies/getMovies");
    this.$refs.list.addEventListener("scroll", this.updateScroll);
    this.$refs.list.scrollBy(0, 0);
    this.wscrollPosition = this.$refs.list.scrollWidth - window.innerWidth;
  },
  computed: {
    movies() {
      return this.$store.getters["movies/movies"].results;
    },
  },
  methods: {
    updateScroll() {
      this.wscrollPosition = this.$refs.list.scrollWidth - window.innerWidth;
      this.scrollPosition = this.$refs.list.scrollLeft;
    },
    prev() {
      this.$refs.list.scrollBy((-window.innerWidth / 100) * 50, 10);
    },
    next() {
      this.$refs.list.scrollBy((window.innerWidth / 100) * 50, 10);
    },
    close() {
      this.selectId = null;
      this.selectMovie = null;
    },
    async getMovie(movie, index) {
      this.selectId = movie.id;
      this.index = index;
      this.selectMovie = null;
      await setTimeout(() => true, 20000)
      await this.$store.dispatch("movie/getMovie", this.selectId);
      this.selectMovie = this.$store.getters["movie/movie"];
      this.selectMovie.release_date =
        this.selectMovie.release_date.split("-")[0];
      this.selectMovie.runtime = this.timeDecoder(this.selectMovie.runtime);
      console.log(this.selectMovie);
    },
    timeDecoder(mins) {
      mins = mins * 60;
      let hou = Math.floor(mins / 60 / 60),
        min = Math.floor(mins / 60) - hou * 60;

      min = min < 10 ? `0${min}` : min;

      const ru = hou > 0 ? `${hou}ч ${min}м` : `${min}м`;
      const en = hou > 0 ? `${hou}h ${min}m` : `${min}m`;
      if (localStorage.lang == "ru") return ru;
      if (localStorage.lang == "en") return en;
    },
  },
};
</script>